.header {
  width: 100%;
  height: 70px;
  position: absolute;
  top: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.1);

  .container {
    margin: auto;
  }

  &, a {
    color: #fff;
    text-decoration: none;
  }

  .logo {
    padding-top: 10px;
  }

  .nav {
    float: right;
    li {
      float: left;
      padding: 0 22px;
      position: relative;
      &:hover > ul {
        display: block;
      }

      //&:hover > a {
      //  color: #00b38a;
      //}
    }
    a {
      display: inline-block;
      line-height: 70px;
      cursor: pointer;
    }
    //a:hover {
    //  color: #00b38a;
    //}

    //.login a {
    //  &::after {
    //    content: '';
    //    display: inline-block;
    //    width: 18px;
    //    height: 18px;
    //    margin-left: 5px;
    //    @extend %home-sprite;
    //    vertical-align: -3px;
    //    background-position: -54px -5px;
    //  }
    //}
  }

  .sub {
    display: none;
    position: absolute;
    margin-bottom: -1px;
    background: #fff;
    padding: 0 10px;
    top: 100%;
    left: 50%;
    width: 140px;
    margin-left: -60px;
    text-align: center;
    z-index: 1;

    li {
      float: none;
      margin: 0;
      padding: 0;
      border-bottom: 1px dotted rgba(0, 179, 138, .3);
    }

    li:last-of-type {
      border: none;
    }

    li:hover a{
      color: #00b38a;
    }

    a {
      display: block;
      width: 100%;
      //background-color: #fff;
      line-height: 40px;
      color: #959494;
    }
  }
}

