@mixin arrow($width: 5px, $color: #fff) {
  display: inline-block;
  width: 0;
  height: 0;
  border: $width solid $color;
  &.left {
    border-color: transparent $color transparent transparent;
  }
  &, &.right {
    border-color: transparent transparent transparent $color;
  }
  &.top {
    border-color: transparent transparent $color transparent;
  }
  &.bottom {
    border-color: $color transparent transparent transparent;
  }
}

@mixin transition($prop...) {
  -webkit-transition: $prop;
  -moz-transition: $prop;
  -ms-transition: $prop;
  -o-transition: $prop;
  transition: $prop;
}

@mixin ie6(){
  html.ie6 & {
    @content;
  }
}

@mixin ie7(){
  html.ie7 & {
    @content;
  }
}

@mixin ie8(){
  html.ie8 & {
    @content;
  }
}

@mixin oldie(){
  html.oldie & {
    @content;
  }
}

@mixin  opacity($opacity) {
  opacity: $opacity / 100;
  filter: alpha(opacity=#{$opacity});
}

@mixin backgroundRGBA($r, $g, $b, $a, $argb){
  background-color: rgb($r, $g, $b);
  background-color: rgba($r, $g, $b, $a);
  @include oldie {
    -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=##{$argb}, endColorstr=##{$argb})";
    filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0,startColorstr='##{$argb}', endColorstr='##{$argb}')
  }
}

@mixin png-repeat($src, $isRepeat:true, $dir: $ieDefaultDir, $hst: $heiyanRes){
  background-image: url($src);
  @include ie6 {
    background: none;
    @if $isRepeat == true {
      filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(enabled=true, sizingMethod='crop', src=#{$hst}#{$dir}#{$src})
    }@else{
      filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(enabled=true, sizingMethod='scale', src=#{$hst}#{$dir}#{$src})
    }
  }
}

@mixin background-size($src) {
  background-image: url(#{$src});
  background-size: cover;
  @include oldie {
    filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='#{$src}', sizingMethod='scale');
    -ms-filter: "progid:DXImageTransform.Microsoft.AlphaImageLoader(src='#{$src}', sizingMethod='scale')";
  }
}