.home-page {
  height: 100%;
  font-weight: 100;

  @mixin bg($img: "people") {
    &.#{$img} {
      background-image: url("../assets/product_" + $img + "@2x.png");
    }
  }

  @mixin bg_active($img: "people") {
    &.#{$img} {
      background-image: url("../assets/product_" + $img + ".active@2x.png");
    }
  }

  h1 {
    @extend .font-size-24px;
    font-weight: 100;
    padding-bottom: 20px;
  }

  h2 {
    @extend .font-size-18px;
    font-weight: 100;
    padding-bottom: 4px;
  }

  p, .desc{
    @extend .font-size-14px;
  }

  .wrap {
    height: 100%;
  }

  .billboard {
    position: relative;
    width: 100%;
    height: 100%;
    color: #ffffff;
    background-color: #000000;
    overflow: hidden;

    &:before {
      content: ' ';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      opacity: 0.8;
      background: url('#{$assets}/home_billboard_bg.jpg') center top no-repeat;
      background-size: cover;
    }

    .slogan {
      z-index: 5;
      //margin-top: 60px;
      //position: relative;
      //padding: 210px;
      text-align: center;
      position: absolute;
      height: 133px;
      left: 0;
      right: 0;
      top: 30%;
      margin: auto;

      h1 {
        @extend .font-size-36px;
        padding-bottom: 10px;
      }

      p {
        @extend .font-size-20px;
        padding-bottom: 20px;
      }

      .try-bi-link {
        margin: 1rem auto;
      }
    }
  }
  //end billboard

  .scene {
    width: 100%;
    background: url("../assets/home_scene_bg.png") no-repeat;
    background-size: cover;
    color: #fff;

    .container {
      @extend .clearfix;
      @extend %center-block;
      @extend .vm-grid;
      text-align: left;

      .hd {
        width: 50%;
      }

      .btn-wp {
        margin-top: 20px;
        text-align: center;
      }

      .bd {
        margin: 60px auto;
      }

      .fg {
        float: right;
      }
    }
  }

  .solution {
    width: 100%;
    //height: 545px;
    color: #959494;
    overflow: hidden;
    padding: 91px 0;
    //background: url("../img/solution_bg.png") no-repeat;
    //background-size: cover;
    background-color: #fff;

    .container {
      text-align: center;

      .solution-link {
        margin: 1rem auto;
        color: #959494;
        border: 1px solid rgba(159, 158, 158, 0.52);

        &:hover {
          //background-color: #00b38a;
          color: #fff;
          //border: 1px solid #fff;
        }
      }

      h1 {
        color: #000;
      }
    }

    .board {
      width: 100%;
      padding: 0 .5rem;
      overflow: hidden;
      margin: 3rem auto 0;

      li {
        float: left;
        border: 1px solid rgba(159,158,158,.52);
        width: 32%;
        height: 110px;
        padding: 0 40px;
      }

      .wrap {
        overflow: hidden;
      }

      .icon {
        float: left;
        margin: 30px .5rem;
      }

      .data {
        margin: 23px 0;
        overflow: hidden;
      }

      .count {
        color: #000;
      }

      p {
        text-align: left;
        @extend .font-size-24px;
        margin-left: 30px;
        margin-bottom: 0;
      }

      p.desc {
        @extend .font-size-14px;
      }

      > li:nth-of-type(2) {
        margin: 0 2%;
      }
    }
  }
  //end solution

  .products {
    width: 100%;
    height: 480px;
    text-align: center;
    padding: 62px 0;
    background-color: #f0f0f0;

    .bd {
      height: 310px;
      color: #00b38a;
      margin: auto;

      >li:nth-of-type(2) {
        margin: 0 2%;
      }

      .cover {
        float: left;
        width: 32%;
        border: 1px solid #c9c9c9;
        height: 100%;
        padding: .5%;
        position: relative;

        a.icon {
          display: block;
          margin: 50px auto 40px auto;
          height: 42px;
          width: 70px;
          background-size: auto 100%;
          background-repeat: no-repeat;
          background-position: center;

          @include bg(people);
          @include bg(chart);
          @include bg(page);
        }

        h2.title {
          @extend .font-size-18px;
          color: #222222;
          margin: 0;
          padding-bottom: 5px;
        }

        p {
          @extend .font-size-14px;
          color: #959494;
        }

        a.btn-ghost {
          @extend .font-size-14px;
          position: absolute;
          border: 1px solid #c9c9c9;
          display: block;
          color: #666666;
          left: 0;
          right: 0;
          bottom: 30px;
          margin: auto;
          height: 40px;
          width: 140px;
          padding: .5rem 1rem;
          box-sizing: border-box;
          text-decoration: none;
        }

        &:hover {
          //cursor: pointer;
          background-color: #00b38a;
          border: none;
          transform: scale(1.1);
          color: #fff;
          transition: all 300ms linear;

          h2.title, p{
            color: #fff;
          }

          a.icon {
            transition: all 400ms linear;

            @include bg_active(people);
            @include bg_active(chart);
            @include bg_active(page);
          }

          a.btn-ghost {
            border: 1px solid #fff;
            color: #fff;
          }
        }
      }
    }
  }
  //  end product
  .our-partner {
    //height: 560px;
    text-align: center;
    width: 100%;
    padding: 62px 0;
    background-color: #fff;

    h1 {
      color: #222222;
    }

    .container {
      position: relative;

      .fa {
        position: absolute;
        display: block;
        @extend .font-size-40px;
        top: 50%;
      }

      .fa-angle-left {
        left: 0;
      }

      .fa-angle-right {
        right: 0;
      }
    }

    ul.partener-board {
      position: relative;
      padding-top: 40px;
      //max-width: 910px;
      //width: 100%;
      margin: auto;
      //overflow-x: hidden;
    }

    li.logo-box {
      position: relative;
      float: left;
      width: #{(100/7) + '%'};
      height: 80px;
      text-align: center;
      padding: 10px 5px;
    }

    //ul li:nth-of-type(2n-1){
    //  background-color: #F5FAF7;
    //}

    .logo-box img {
      vertical-align: inherit;
      width: 80%;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: auto;
    }

  }

  .functions {
    padding: 50px 0;
  }

  .function {
    @extend .vm-grid;
    width: 90%;
    padding: 10px 0;
    margin: auto;

    h5 {
      @extend .font-size-18px;
      padding-bottom: 20px;
      color: #222222;
    }
    p {
      @extend .font-size-14px;
      color: #959494;
    }
    .content {
      overflow: hidden;
      @extend .f1;
    }
    figure {
      display: inline-block;
      width: 342px;
      height: 342px;
      //margin-left: 76px;
      overflow: hidden;
      border: 1px solid #ddd;
      box-shadow: 2px 2px 2px #ddd;
      border-radius: 100%;
    }
  }
  .figure-left {
    .img {
      margin-left: 0;
      margin-right: 76px;
    }
  }

}




