.about-page {
  h1 {
    font-size: 24px;
    font-weight: 100;
    padding-bottom: 20px;
  }

  p, .desc {
    font-size: 14px;
    font-weight: 100;
  }

  .billboard {
    position: relative;
    width: 100%;
    height: 530px;
    color: #ffffff;
    background-color: #000000;
    overflow: hidden;

    &:before {
      content: ' ';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      background: #44485b url('../assets/about_billboard_bg.png') 50% 55% no-repeat;
      background-size: auto 320px;
    }

    .slogan {
      z-index: 5;
      position: relative;
      padding-top: 150px;
      text-align: center;

      h2 {
        @extend .font-size-36px;
        padding-bottom: 17px;
        font-weight: 100;
      }

      p {
        @extend .font-size-18px;
        font-weight: 100;
      }

      .try-bi-link {
        text-align: center;
        margin: 1rem auto;
      }
    }
  }
  //end billboard

  .development {
    //height: 540px;
    padding: 60px 0;
    text-align: center;

    .title {
      font-size: 24px;
      color: #222222;
      margin: 0;
    }

    .container {
      position: relative;
    }

    .timeline {
      position: relative;

      img {
        width: 100%;
      }

      .milestone {
        position: absolute;
        width: 200px;
        text-align: left;

        .time {
          font-weight: bold;
          color: #222222;
        }

        .event {
          color: #959494;
        }
      }

      .first {
        top: 5%;
        left: 17%;
      }

      .second {
        bottom: 5%;
        right: 68%;
        text-align: right;
      }

      .third {
        top: 7%;
        left: 54%;
      }

      .fourth {
       bottom: 5%;
        left: 66%;
      }

      .fifth {
        top: 9%;
        left: 77%;
      }
    }
  }

  .team {
    background-color: #f0f0f0;
    padding: 111px 0;
    height: 900px;
    text-align: center;
    color: #222222;

    .title {
      font-size: 24px;
      margin: 0;
    }

    .avatar-board {
      overflow: hidden;
      width: 1000px;
      margin: auto;

      li {
        float: left;
        width: 230px;
        height: 272px;
        background-color: #fff;
        margin: 0 20px 40px 0;
        box-shadow: 0 0 10px #d5d5d5;

        .avatar-wrap {
          margin: 10px auto;
          width: 160px;
          height: 160px;
          overflow: hidden;
          border-radius: 50%;
          border: 10px solid #f0f0f0;
        }
      }
    }
  }

  .location {
    height: 500px;
    padding: 50px 0;
    background-color: #f0f0f0;

    a {
      color: #666666;
      text-decoration: none;
    }

    a:hover {
      color: #00b38a;
    }

    .find-us {
      position: relative;
      height: 100%;

      .bd {
        margin: 124px 0;
        width: 45%;
      }

      h1 {
        @extend .font-size-20px;
      }

      .info {
        color: #666666;
        font-size: 20px;
        line-height: 25px;

        .title {
          font-weight: bold;
        }
      }
    }

    #map {
      height: 100%;
      width: 50%;
      right: 15px;
      margin-top: -124px;
      position: absolute;
      box-shadow: 2px 2px 2px rgba(0,0,0,.1);
    }
  }
}




