.footer {
  height: 300px;
  padding-top: 80px;
  position: relative;
  color: #c5c7cb;
  @extend .font-size-12px;
  background-color: #272a31;

  a {
    color: #c5c7cb;
    text-decoration: none;
  }

  a:hover {
    color: #00b38a;
  }

  .about-company {
    width: 90%;

    .main {
      overflow: hidden;

      li {
        margin-bottom: 10px;
      }

      li.part {
        float: left;
        width: 20%;
      }
    }
  }

  .left {
    padding-right: 70px;
  }

  .right {
    float: right;
  }

  h4 {
    padding-bottom: 16px;
    margin-bottom: 16px;
    color: #fff;
    @extend .font-size-18px;
  }

  .contact-us {
    i {
      display: inline-block;
      width: 20px;
      height: 20px;
      @extend %home-sprite;
      vertical-align: middle;
    }
    .telephone i {
      background-position: -5px -6px;
    }
    .email i {
      background-position: -28px -6px;
    }
  }

  .address {
    padding: 0 0 16px 5px;
    border-bottom: 1px dotted #676f78;
  }

  .copyright {
    height: 68px;
    line-height: 68px;
    padding-bottom: 60px;
    color: #c5c7cb;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;

    @extend .font-size-14px;

    .content {
      margin: 0 auto;
      text-align: center;
      min-width: 552px;
      max-width: 610px;

      .record {
        float: right;
      }
    }
  }

  .qr {
    width: 100px;
    height: 100px;
    img {
      width: 100%;
    }
  }

  .contact-us-left {
    float: left;
    text-align: right;

    img.logo {
      margin-bottom: 10px;
    }

    p {
      margin-bottom: 10px;
    }
  }
  .contact-us-right {
    padding-left: 30px;
    padding-top: 10px;
    overflow: hidden;
    zoom: 1;
    text-align: center;
  }
}